<template>
  <div class="notification" :class="notificationClass">
    <div class="message">{{ message }}</div>
    <div class="button-container">
      <!-- <button class="close-button" @click="handleLogin">Login</button> -->
      <button
        class="close-button"
        @click="handleSignIn"
        :disabled="!Vue3GoogleOauth.isAuthorized"
      >
        Sign In
      </button>
    </div>
  </div>
</template>

<script lang="js">
import { defineComponent, inject } from "vue";

export default defineComponent({
  data() {
  return {
    message: "Login your google account",
    isActive: true,
    timeout: null,
  };
},
  props: {
    emitLogin: {
      type: Function,
      required: true,
    },
  },
  computed: {
    notificationClass(){
      return this.isActive ? "active" : "";
    },
  },
  mounted() {
    this.timeout = setTimeout(() => {
      this.hideNotification();
    }, 10000);
  },
  beforeUnmount() {
    if (this.timeout) {
      clearTimeout(this.timeout);
    }
  },
  setup() {
    const Vue3GoogleOauth = inject('Vue3GoogleOauth');

    return {
      Vue3GoogleOauth,
    };
  },
  methods: {
    hideNotification() {
      this.isActive = false;
    },
    handleLogin() {
      this.hideNotification();
      this.emitLogin(); // Emit the "login" event using the prop function
    },
    async handleSignIn() {
      try {
        const googleUser = await this.$gAuth.signIn();
        // console.log(this.$gAuth.signIn);

        if (!googleUser) {
          return null;
        }

        this.hideNotification();
        this.emitLogin();
        this.user = googleUser.getBasicProfile().getEmail();
      } catch (error) {
        console.log(error);
        return null;
      }
    },
  },
});
</script>

<style scoped>
.notification {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #333;
  color: #fff;
  padding: 10px;
  border-radius: 4px;
  z-index: 9999;
  opacity: 0;
  transition: opacity 0.3s;
  width: 400px;
  height: 100px;
  font-size: 32px;
}

.notification.active {
  opacity: 1;
}

.message {
  margin-bottom: 10px;
}

.button-container {
  display: flex;
  justify-content: flex-end;
}

.close-button {
  background: none;
  border: none;
  color: #fff;
  cursor: pointer;
  font-size: 32px;
}
</style>
